import Footer from 'components/Footer';
import Header from 'components/Header';
import Content from 'components/UI/Content';
import useAppDispatch from 'helpers/hooks/useAppDispatch';
import routes from 'helpers/routes';
import { SuisseIntl } from 'helpers/utils/fonts';
import { serverSideTranslations } from 'next-i18next/serverSideTranslations';
import Link from 'next/link';
import React from 'react';
import { changeIsMobile } from 'store/isMobile/IsMobileStore';
import styles from 'styles/fourOhFour.module.scss';

const FourOhFour = () => {
	const dispatch = useAppDispatch();

	React.useEffect(() => {
		dispatch(changeIsMobile(document.documentElement.scrollWidth < 1024));
	}, []);

	return (
		<div className={styles.fourOhFourContainer}>
			<Header />
			<Content className={styles.fourOhFour}>
				<>
					<h1 className={SuisseIntl.className}>404</h1>
					<p>Страница не найдена</p>
					<Link href={routes.home}>Перейти на главную</Link>
				</>
			</Content>
			<Footer className={styles.footer} withDarkBGC={true} />
		</div>
	);
};

export const getStaticProps = async ({ locale }) => {
	return {
		props: {
			...(await serverSideTranslations(locale, ['common'])),
		},
	};
};

export default FourOhFour;
